import React from 'react';
import Layout from '../../components/Layout';
import './Resume.css';


const Resume = ({ user }) => {
  return (
    <Layout user={user}>
      <div className='resume-wrapper'>

          <div className='skills-area padding-top mi-section'>
            <div className='skills-container'>
              <div className='section-title'>
                <h2>My Skills</h2>
              </div>

              <div className='mi-skills'>
                <div className='row'>

                  <div className="col-lg-6 mt-30">
                    <div className="mi-progress">
                      <h6 className="mi-progress-title">Python</h6>
                      <div className="mi-progress-inner">
                        <div className="mi-progress-percentage">95%</div>
                        <div className="mi-progress-container">
                          <span className="mi-progress-active" style={{width: "95%"}}></span>
                        </div>
                      </div>
                    </div>
                  </div>
                
                  <div className="col-lg-6 mt-30">
                    <div className="mi-progress">
                      <h6 className="mi-progress-title">JavaScript</h6>
                      <div className="mi-progress-inner">
                        <div className="mi-progress-percentage">95%</div>
                        <div className="mi-progress-container">
                          <span className="mi-progress-active" style={{width: "20%"}}></span>
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className="col-lg-6 mt-30">
                    <div className="mi-progress">
                      <h6 className="mi-progress-title">Python</h6>
                      <div className="mi-progress-inner">
                        <div className="mi-progress-percentage">95%</div>
                        <div className="mi-progress-container">
                          <span className="mi-progress-active" style={{width: "95%"}}></span>
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className="col-lg-6 mt-30">
                    <div className="mi-progress">
                      <h6 className="mi-progress-title">Python</h6>
                      <div className="mi-progress-inner">
                        <div className="mi-progress-percentage">95%</div>
                        <div className="mi-progress-container">
                          <span className="mi-progress-active" style={{width: "95%"}}></span>
                        </div>
                      </div>
                    </div>
                  </div>

                </div>
              </div>
            </div>
          </div>

          <div className='resume-area padding-top mi-section mi-padding-bottom'>
            <div className='resume-container'>
              <div className='section-title'>
                  <h2>Resume</h2>
              </div>
           </div>
          </div>

      </div>
    </Layout>
  );
};

export default Resume;
